.nav * {
    box-sizing: border-box;
}

.nav {
    display: inline-flex;
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    max-width: 430px;
    background-color: #fff;
    padding:0 20px;
    border-radius: 40px;
    box-shadow: 0 10px 40px rgba(159, 162, 177, .8);
}

.nav-item{
    color:#83818c;
    padding: 20px;
    text-decoration: none;
    transition: .3s;
    margin: 0 6px;
    z-index: 1;
    font-family: 'DM Sans', sans-serif;
    font-weight: 500;
    position: relative;

    &:before{
        content: "";
        position: absolute;
        bottom: -6px;
        left: 0;
        width: 100%;
        height: 5px;
        background-color: #dfe2ea;
        border-radius: 8px 8px 0 0;
        opacity: 0;
        transition: .3s;
    }
}

.nav-item:not(.is-active):hover:before {
    opacity: 1;
    bottom: 0;
}

.nav-item:not(.is-active):hover {color: #333;}

.nav-indicator{
    position: absolute;
    left:0;
    bottom: 0;
    height: 4px;
    transition: .4s;
    z-index: 1;
    border-radius: 8px 8px 0 0;
}

.nav-item.is-active-orange {
    color: orange;
  }
  
  .nav-item.is-active-green {
    color: green;
  }
  
  .nav-item.is-active-blue {
    color: blue;
  }
  
  .nav-item.is-active-rebeccapurple {
    color: rebeccapurple;
  }
  

@media (max-width: 375px){
    .nav-item{
        padding: 10px;
    }
    
}



